//
const state = {
    //
    userInfo: {}
}
const actions = {
    //
    renewState(store, data) {
        //state
        if (data) {
            store.commit('setState', data);
        }
    }
}
const mutations = {
    setState(state, data) {
        state.userInfo = data;
    }
}
const getters = {
    isLogin(state) {
        return Object.hasOwn(state.userInfo, 'token') ? true : false;
    }
}
export default {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
}