/**
 * 
 * @param value 
 */
export function bigNumberTransform(value) {
    if (typeof value == 'string') {
        value = Number(value);
    }
    let unit = '';
    let k = 10000;
    let sizes = ['', '', '', ''];
    let i;
    if (value < k) {
        value = value;
    } else {
        i = Math.floor(Math.log(value) / Math.log(k));
        value = ((value / Math.pow(k, i))).toFixed(2);
        unit = sizes[i];
    }
    return value + unit;
};
