//
// http://localhost:3000/personalized limit 
import requests from "./request";
export function reqRecommendPlayList() {
    return requests.get('/api/personalized', {
        method: 'get',
        params: {
            limt: 18
        }
    })
}

// /top/album 
export const reqAlbumList = () => requests.get('/api/top/album', {
    method: 'get'
});

